//import { HashRouter, HashRouter as Router, Route } from 'react-router-dom'
import { Component } from 'react'
import request from "request";
import { Button, Form, Card } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import './login.css'
class Login extends Component {
    constructor(props) {
        super(props)

        this.state = {
            username: '',
            password: '',
            token: '',
            namespace: '',
            message:''
        }
        this.onEmail = this.onEmail.bind(this);
        this.onPassword = this.onPassword.bind(this);
        this.onClickLogin = this.onClickLogin.bind(this);
    }
    onEmail(e) {
        this.setState({ username: e.target.value })
    }
    onPassword(e) {
        this.setState({ password: e.target.value })
    }
    onClickLogin() {
        const AuthPromise = new Promise((resolve, reject) => {
            request.get(`https://auth.nyaator.co/auth?username=${this.state.username}&password=${this.state.password}`, (err, response, body) => {
                resolve(response)
            })
        })

        AuthPromise
            .then(data=>{
                if(data.statusCode === 200){
                    const secret = JSON.parse(data.body)
                    this.setState({token:secret.apikey,namespace:secret.nameSpace})
                    window.location.assign(`/#/testmail/${this.state.token}/${this.state.namespace}/`);
                }
                else{
                    this.setState({message:"Check your username/password"})
                }
            })
    }
    render() {
        return (
            <div className='toor'>
                <Card style={{ padding: '40px', backgroundColor: '#282c34', boxShadow: '0px 0px 100px 0px black' }}>
                    <Form>
                        <Form.Group controlId='formBasicInput'>
                            <Form.Control type="email" placeholder="Enter email" onChange={this.onEmail} />
                        </Form.Group>
                        <Form.Group controlId='formBasicPassword'>
                            <Form.Control type="email" placeholder="Enter password" onChange={this.onPassword} />
                        </Form.Group>
                    </Form>
                    <Button variant="primary" type="submit" onClick={this.onClickLogin}>
                        Login
                    </Button>
                    <span>{this.state.message}</span>
                </Card>
            </div>
        )
    }
}

export default Login;
