import { Component } from 'react'
import { Table, Navbar } from 'react-bootstrap';
import './home.css'
class Home extends Component {
    constructor(props) {
        super(props);

        this.state = {
            emails: []

        }
    }
    componentDidMount() {
        const request = require('request')
        const NAMESPACE = this.props.match.params.namespace
        const APIKEY = this.props.match.params.api
        let url = `https://api.testmail.app/api/json?apikey=${APIKEY}&namespace=${NAMESPACE}&pretty=true`
        const testMailApiPromise = new Promise((resolve, reject) => {
            request.get(url, (err, response, body) => {
                resolve(response)
            })
        })

        testMailApiPromise.then(ad => {
            const parsed = JSON.parse(ad.body)
            this.setState({ emails: parsed.emails })
        })
    }
    render() {
        return (
            <div className='home'>
                <Navbar bg='dark' style={{ borderRadius: '00px 0px 10px 10px', boxShadow: '0px 0px 15px 0px black' }}>
                    <Navbar.Brand style={{ fontWeight: 'bolder', fontSize: '1.5em', backgroundColor: 'transparent' }}>TestMail</Navbar.Brand>
                </Navbar>
            <div className='list-view'>
                <Table responsive variant='dark' striped bordered hover contentEditable='false'>

                    <tr className='headers'>
                        <th colSpan='1'>From</th>
                        <th>Content</th>
                        <th>Tag</th>
                        <th>Link</th>
                    </tr>
                        {this.state.emails.map(tb=>(
                            <tbody>
                        <td>{tb.envelope_from}</td>
                        <td>Subject: <b>{tb.subject}</b> <br></br><br></br> {tb.text} </td>
                        <td>{tb.tag}</td>
                        <td><a href={tb.downloadUrl}>Download</a></td>
                    </tbody>
                        ))}
                </Table>
            </div>
            </div>
        )
    }
}

export default Home;