import { HashRouter,Route, Switch }from 'react-router-dom'
import {Component} from 'react'
import Login from './components/login';
import './App.css'
import Home from './components/home'
class App extends Component{
  render(){
    return(
      <HashRouter>
        <Switch>
        <Route 
        path='/'
        exact
        component={Login}
        />
        <Route
        path='/testmail/:api/:namespace/'
        exact
        component={Home}
        />
        </Switch>
      </HashRouter>
    )
  }
}

export default App;
